}
.Middle-Wear {
  display: flex;
  flex-direction: column; /* Change to column on small screens */
  grid-gap: 20px;
  gap: 20px;
  box-shadow: 10px 10px greenyellow;
}

.Branda {
  max-width: 100%;
  min-width: calc(20em); /* Take full width of the container */
  height: auto; /* Maintain aspect ratio */
}

.Goofy {
  line-height: 1.350em;
  display: flex;
  flex-direction: column;
  font-size: 19px; 
  font-family: 'Cormorant Garamond', serif;
  font-family: 'Poppins', sans-serif;/* Use the default font size */
}

@media (min-width: 768px) {

  /* Apply these styles on screens wider than 768px */
  .Middle-Wear {
    flex-direction: row;
    font-size: calc(2em); /* Revert to row layout on larger screens */
  }
}
.Newspaper{
    height: 115vh;
    background-color: #f9f7f1;
    font-family: 'Driod Serif';
    border-radius: 0.5%;
    color: #2f2f2f;

}
.header{
    font-family: 'Playfair Display',serif;
    font-weight: 900;
    font-size: 80px;
    text-transform: uppercase;
    color: #2f2f2f;
    text-align: center;
}

.subhead{
    text-transform: uppercase;
    border-bottom: 2px solid #2f2f2f;
    border-top: 2px solid #2f2f2f;
    color: black;
    text-align: center;
}

.content{
    font-size: 0;
    line-height: 0;
    word-spacing: -.25em;
    display: flex;
    flex-direction: row;
    margin: 40px 2% 0 2%;
    text-align:inherit;
}

.colo{
    font-size: 14px;
    line-height: 25px;
    width: 47%;
    display: inline-block;
    vertical-align: top;
    margin-bottom: 20px;
    transition: all 7.5;
    margin-left: calc(2%);

}
.colo > h1, .para {
    color: #2f2f2f;
    margin-left: 20px;

}
.para {
    font-family: 'Playfair Display', serif;
    color: #2f2f2f;
    max-height: 60vh;
    overflow-y: auto;
    word-break: break-all; /* This will allow long words to break */
}


.colo + .colo{
    border-left: 1px solid #2f2f2f;

}
.colo .headline{
    text-align: center;
    line-height: normal;
    font-family: 'Playfair Display', serif;
    margin: 0 auto;
    color: #2f2f2f;

}


